5px;\r\n    display: flex;\r\n}\r\n.rightHeader {\r\n    flex: 1;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: end;\r\n    gap: 35px;\r\n}\r\n.rightHeader ul {\r\n    margin: 0;\r\n    padding: 0;\r\n    list-style: none;\r\n    display: flex;\r\n    gap: 56px;\r\n}\r\n.rightHeader ul li a{\r\n    color: #fff;\r\n    font-family: 'Poppins', sans-serif;\r\n    font-size: 18px;\r\n}\r\n.rightHeader ul li.active a{\r\n  font-weight: bold;\r\n}\r\n.about-sec {\r\n    padding: 80px 0 43px 0;\r\n    text-align: center;\r\n}\r\n.about-sec p{\r\nfont-family: 'Roboto', sans-serif;\r\nfont-size: 24px;\r\nline-height: 28px;\r\ntext-align: center;\r\ncolor: #000000;\r\nmargin-bottom: 19px;\r\n}\r\n.btn-primary{\r\n  background: #D62222;\r\n  border-radius: 49px;\r\n  padding:20px 100px;\r\n  font-size: 18px;\r\n  display: inline-block;\r\n  color: #fff;\r\n  border:1px solid  #D62222;\r\n  cursor: pointer;\r\n  transition: 0.3s all;\r\n  -moz-transition: 0.3s all;\r\n  -webkit-transition: 0.3s all;\r\n}\r\n.btn-primary:hover{\r\n  background-color: #fff;\r\n  color:  #D62222;\r\n}\r\n.filter-section{\r\n  position: relative;\r\n  overflow: hidden;\r\n  padding: 84px 0 26px;\r\n}\r\n\r\n.filter-section input[type=text] {\r\n  width: 500px;\r\n  font-size: larger;\r\n}\r\n.filter-section:before {\r\n    width: 225px;\r\n    height: 100%;\r\n    position: absolute;\r\n    content: '';\r\n    background-image: url('./images/pinktriangle.png');\r\n    background-size: contain;\r\n    background-repeat: no-repeat;\r\n    background-position:top left;\r\n    top: 0;\r\n    left: 0;\r\n}\r\n.filter-section:after {\r\n    width: 225px;\r\n    height: 100%;\r\n    position: absolute;\r\n    content: '';\r\n    top: 0;\r\n    right: 0;\r\n    background-image: url('./images/bluetriangle.png');\r\n    background-size: contain;\r\n    background-repeat: no-repeat;\r\n    background-position:top right;\r\n}\r\n.filter-section h3{\r\nfont-weight: 600;\r\nfont-size